import * as Yup from 'yup';
import { Titular } from '../../../models/Associados/Titular';
import { Associado } from '../../../models/Associados/Associado';


export const TitularSchema = Yup.object().shape<Titular>({
    nome: Yup.string()
        .max(50, "O campo [Nome] no deve possuir mais de 50 caracteres.")
        .required('[Nome] do Titular no pode ser vaziu.'),
    rg: Yup.string()
        .max(50, "O campo [RG] no deve possuir mais de 20 caracteres.")
        .required('[RG] do Dependente no pode ser vaziu.'),
    cpf: Yup.string()
        .max(50, "O campo [CPF] no deve possuir mais de 20 caracteres.")
        .required('[CPF] do Dependente no pode ser vaziu.'),
});

export const AssociadoSchema = Yup.object().shape<Associado>({
    nome: Yup.string()
        .max(50, "O campo [Nome] no deve possuir mais de 50 caracteres.")
        .required('[Nome] do Titular no pode ser vaziu.'),

    rg: Yup.string()
        .max(50, "O campo [RG] no deve possuir mais de 20 caracteres.")
        .required('[RG] do Dependente no pode ser vaziu.'),

});