import * as Yup from 'yup';
import { Cidade } from '../../../../models/Cidades/Cidade';


export const CidadeSchema = Yup.object().shape<Cidade>({
    nome: Yup.string()
        .max(50, "O campo [Nome] no deve possuir mais de 50 caracteres.")
        .required('[Nome] da Cidade no pode ser vaziu.'),

    ddd: Yup.string()
        .max(5, "O campo [DDD] no deve possuir mais de 5 caracteres.")
        .required('[DDD] do Cidade no pode ser vaziu.'),

    estadoId: Yup.number()
        .nullable()
        .typeError("Id invlido")
        .required('O campo Estado  obrigatrio'),
});