import * as Yup from 'yup';
import { Pais } from '../../../../models/Cidades/Pais';


export const PaisSchema = Yup.object().shape<Pais>({
    nome: Yup.string()
        .max(50, "O campo [Nome] no deve possuir mais de 50 caracteres.")
        .required('[Nome] do pais no pode ser vaziu.'),

    ddi: Yup.string()
        .max(5, "O campo [DDI] no deve possuir mais de 5 caracteres.")
        .required('[DDI] do pais no pode ser vaziu.'),

    sigla: Yup.string()
        .max(5, "O campo [Sigla] no deve possuir mais de 5 caracteres.")
        .required('[Sigla] do pais no pode ser vaziu.'),
});